<template>
    <rect 
      :x="x"
      :y="y"
      :rx="h/2"
      :width="w" 
      :height="h" 
      :stroke="color"
      :fill="fill[card.shading]"
      stroke-width="3px"
    />
</template>

<script>

export default {
  name: 'SetOval',
  props: {
    x: Number,
    y: Number,
    w: Number,
    h: Number,
    card: Object,
    color: String,
  },
  computed: {
    fill() {
      return ['#fff',
        this.color,
        `url('#diagonalHatch${this.card.color}')`,
      ];
    }
  },
}
</script>

