<template>
    <path
      :d="d"
      :fill="fill[card.shading]"
      :stroke="color"
      stroke-width="3px"
      :style="{'transform': `translate(${50}%, ${0.75*y+15}%)`}"
    />
</template>

<script>

export default {
  name: 'SetSquiggle',
  props: {
    x: Number,
    y: Number,
    w: Number,
    h: Number,
    card: Object,
    color: String,
  },
  computed: {
    d() {
      return `M ${-14} ${14} `
          + `C -2 14, -2 8, 10 8 `
          + `C 22 8, 20 14, 28 14 `
          + `C 40 14, 30 -14, 14 -14 `
          + `C 2 -14, 2 -8, -10 -8 `
          + `C -22 -8, -20 -14, -28 -14 `
          + `C -40 -14, -30 14, -14 14`;
    },
    fill() {
      return ['#fff0',
        this.color,
        `url('#diagonalHatch${this.card.color}')`,
      ];
    }
  },
}
</script>

